import React from 'react';
import './App.css';
import yosano_akiko_img from './img/yosanoakiko.jpeg'

class App extends React.Component {
  render() {
    return (
        <div className="App">
          <header className="App-header">
          <img src={yosano_akiko_img} alt="与謝野晶子" />
            <p>俳句のクオリティチェック！？与謝野晶子診断</p>
          </header>
          <p>俳句を入力してね</p>
          {Form()}
          <Body/>
        </div>
      );
  }
}

class Body extends React.Component {
  constructor(props) {
    super(props)
    this.state = {value: null}
  }

  getResult() {
    this.setState({value: Result()});
  }

  getRetry() {
    this.setState({value: null});
  }

  render() {
    return (
      <div>
        <div>
          <button link="#result" value="送信" onClick={() => this.getResult()}>送信</button>
          <button link="#result" value="やり直す" onClick={() => this.getRetry()}>やり直す</button>
        </div>
        <div>{this.state.value}</div>
      </div>
    );
  }
}

function Form() {
  return (
    <div className="pre-line">
      <p>上五<input type="text" name="kaminoku"/></p>
      <p>中七<input type="text" name="nakashichi"/></p>
      <p>下五<input type="text" name="shumogo"/></p>
    </div>
  );
}

function Result() {
  let point = Math.round(Math.random() * 100);
  return (
      <p>{point}点です</p>
  );
}
export default App;
